import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormControl, FormGroup } from "@angular/forms";

@Component({
  selector: "mainapp-benhnhan",
  templateUrl: "./benhnhan.component.html",
  styleUrls: ["./benhnhan.component.less"],
})
export class BenhnhanComponent implements OnInit {
  validateForm: FormGroup;

  controlArray: any[] = [
    { title: "Mã bệnh nhân", col: 6, property: "id" },
    { title: "Tên bệnh nhân", col: 12, property: "hoTen" },
    { title: "Giới tính", col: 6, property: "gioiTinh" },
    { title: "Ngày sinh", col: 6, property: "ngaySinh" },
    { title: "Tuổi", col: 6, property: "tuoiBN" },
    { title: "Nghề nghiệp", col: 12, property: "ngheNghiep" },
    { title: "Người nhà", col: 6, property: "nguoiNha" },
    { title: "Số BHYT", col: 6, property: "soBHYT" },
    { title: "Mức hưởng", col: 6, property: "mucHuong" },
    { title: "Nơi KCBBD", col: 12, property: "noiKCBBD" },
    { title: "Tuyến BHYT", col: 6, property: "tuyenBHYT" },
    { title: "Hạn thẻ", col: 6, property: "hanThe" },
    { title: "Đón tiếp lúc", col: 6, property: "donTiepLuc" },
    { title: "Loại đón tiếp", col: 6, property: "loaiDonTiep" },
    { title: "Đối tượng", col: 6, property: "doiTuong" },
    { title: "Lý do khám", col: 6, property: "lyDoKham" },
    { title: "Mã bệnh án", col: 6, property: "maBenhAn" },
    { title: "Số vào viện", col: 6, property: "soVaoVien" },
    { title: "Số vào khoa", col: 6, property: "soVaoKhoa" },
    { title: "Mã điều trị", col: 6, property: "maDieuTri" },
    { title: "Khoa phòng", col: 12, property: "khoaPhong" },
    { title: "Bác sĩ khám", col: 6, property: "bacSiKham" },
    { title: "Điều dưỡng", col: 6, property: "dieuDuong" },
    { title: "Bệnh án", col: 6, property: "benhAn" },
    { title: "Ngày vào", col: 6, property: "ngayVao" },
    { title: "Ngày ra", col: 6, property: "ngayRa" },
    { title: "Xử trí", col: 6, property: "xuTri" },
    { title: "Chẩn đoán", col: 12, property: "chanDoan" },
  ];

  data = {
    id: 'BN001',
    hoTen: 'Nguyễn Khánh Ngọc',
    gioiTinh: 'Nữ',
    ngaySinh: '01/01/1990',
    tuoiBN: 30,
    ngheNghiep: 'Bác sĩ',
    nguoiNha: 'Nguyễn Thị An',
    soBHYT: '1234567890',
    mucHuong: '80%',
    noiKCBBD: 'Bệnh viện Bưu Điện',
    tuyenBHYT: 'Trung ương',
    hanThe: '01/01/2025',
    donTiepLuc: '08:00',
    loaiDonTiep: 'Khám bệnh',
    doiTuong: 'BHYT',
    lyDoKham: 'Khám định kỳ',
    maBenhAn: 'BA001',
    soVaoVien: 'VV001',
    soVaoKhoa: 'VK001',
    maDieuTri: 'DT001',
    khoaPhong: 'Khoa nội',
    bacSiKham: 'BS Nguyễn Văn Tường',
    dieuDuong: 'DD Nguyễn Thị Dung',
    benhAn: 'BA001',
    ngayVao: '01/06/2024',
    ngayRa: '05/06/2024',
    xuTri: 'Điều trị ngoại trú',
    chanDoan: 'Cảm cúm'
  };

  constructor(private fb: FormBuilder) {}

  ngOnInit(): void {
    this.validateForm = this.fb.group({});
    for (let i = 0; i < this.controlArray.length; i++) {
      this.controlArray[i].index = i;
      this.validateForm.addControl(`field${i}`, new FormControl());
    }

    // Đổ dữ liệu vào form
    for (let i = 0; i < this.controlArray.length; i++) {
      let controlName = `field${i}`;
      let property = this.controlArray[i].property;
      if (property && this.data[property]) {
        this.validateForm.controls[controlName].setValue(this.data[property]);
      }
    }
  }
}