  isClose = false;
  col = 3;
  id = -1;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private viewContainerRef: ViewContainerRef
  ) {}

  onResize({ col }: OHResizeEvent): void {
    cancelAnimationFrame(this.id);
    this.id = requestAnimationFrame(() => {
      if (!this.isClose) this.col = col!;
    });
  }

  ngAfterViewInit(): void {
    window.dispatchEvent(new Event("resize"));
    this.loadComponents(0, this.topTabs);
  }

  ngOnInit(): void {}

  loadComponents(index: any, list: any[]) {
    const element = document.getElementById(list[index].queryParam);
    if (element) {
      const elementRef =
        this.viewContainerRef.element.nativeElement.querySelector(
          `#${list[index].queryParam}`
        );
      if (elementRef) {
        const componentFactory =
          this.componentFactoryResolver.resolveComponentFactory(
            list[index].component
          );
        elementRef.innerHTML = "";

        const componentRef = this.viewContainerRef.createComponent(
          componentFactory,
          0
        );
        if (componentRef.instance["benhNhan"] && this.benhNhan) {
          componentRef.instance["benhNhan"] = this.benhNhan;
        }
        console.log(componentRef);

        componentRef.changeDetectorRef.detectChanges();
        elementRef.appendChild(componentRef.location.nativeElement);

        return componentRef.instance;
      }
    }
    return null;
  }
